import React, {useEffect} from 'react';
import '../App.css';
import PrimarySearchAppBar from "../components/appBar/AppBar";
import {useDispatch, useSelector} from "react-redux";
import {CustomizedSnackbars} from "../components/snackBar/snackbar";
import TodolistList from "../components/features/todolists/todolistList/TodolistList";
import {Login} from "../components/features/login/Login";
import {Navigate, Route, Routes} from "react-router-dom";
import {initializeAppTC, logout} from "../components/features/login/LoginReducer";
import {AppRootStateType} from "../state/store";
import {CircularProgress} from "@mui/material";


function AppWithRedux() {
    const isInitialized = useSelector<AppRootStateType, boolean>(state => state.app.isInitialized)
    let dispatch = useDispatch()
    useEffect(() => {
        dispatch(initializeAppTC())


    }, [])

    if (!isInitialized) {
        debugger
        return <div
            style={{position: 'fixed', top: '30%', textAlign: 'center', width: '100%'}}>
            <CircularProgress/>
        </div>
    }

    return (

        <div className="App">

            <PrimarySearchAppBar/>
            <CustomizedSnackbars/>
            <Routes>
                <Route path={''} element={<TodolistList/>}/>
                <Route path={'login'} element={<Login/>}/>
                <Route path={'404'} element={<div><h1>404 page not found</h1></div>}/>
                <Route path={'*'} element={<Navigate to={''}/>}/>

            </Routes>
        </div>

    );
}

export default AppWithRedux;
